import { Component, OnInit, ElementRef } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import * as $ from 'jquery';


@Component({
  selector: 'app-signmodal',
  templateUrl: './signmodal.component.html',
  styleUrls: ['./signmodal.component.css']
})
export class SignmodalComponent implements OnInit {
  registerForm:FormGroup;
  submitted=false;
  
  f_c_n_n:string="name";
  f_c_n_p_e:string="phone";
  show=true;
  
  constructor(private elementRef:ElementRef,private formBuilder:FormBuilder) { }
  

  ngOnInit() {
    this.elementRef.nativeElement.focus();
    
    this.mobunumber();
    
    
    this.placeholder_change();

    
$(document).ready(function(){
  $('#b111').click();
  
});

  }

 

  ngAfterViewInit(){
    var jquery = document.createElement('script');
    jquery.type='text/javascript';
    this.elementRef.nativeElement.appendChild(jquery);
  }

  get f(){
    return this.registerForm.controls;
  }

  

  get ff(){
    return this.registerForm;
  }

 

  mobunumber(){
    this.registerForm=this.formBuilder.group({
      name:['',Validators.required],
      phone:['',[Validators.required,Validators.minLength(10),Validators.maxLength(10),Validators.pattern('[0-9]*')]]
    });

  }

  emailss(){
    this.registerForm=this.formBuilder.group({
      name:['',Validators.required],
      phone:['',[Validators.required,Validators.email]]
    });

  }
  
  onSubmit(){

    this.submitted=true;

    if(this.registerForm.invalid){
      return;
    }

    console.log('name='+this.f.name.value);
    console.log('phone='+this.f.phone.value);
  }

  type_msg_change:string="Use email instead.";
  type_change(){
    if(this.type_msg_change === "Use email instead.")
    {
      
        this.type_msg_change="Use phone instead.";
        this.placeholder_change();
    }
    else{
      this.type_msg_change="Use email instead.";
      this.placeholder_change();
    }
  }

  type_msg_placeholder:string="Phone";
  placeholder_change(){
    if(this.type_msg_change === "Use email instead."){
      this.mobunumber();
      this.show=true;
      this.type_msg_placeholder="Phone";
    }
    else{
      this.type_msg_placeholder="Email";
      this.emailss();
      this.show=false;
    }

  }

}



