import { Component, OnInit, ElementRef } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import * as $ from 'jquery';

@Component({
  selector: 'app-messages',
  templateUrl: './messages.component.html',
  styleUrls: ['./messages.component.css']
})
export class MessagesComponent implements OnInit {

  
  constructor(private elementRef:ElementRef,private obj:HttpClient) { }

  ngOnInit() {
    this.mynewss();
    this.follows();
    this.tweets();

    $(document).ready(function(){

      
        $('#b111').click();
      
      

      $("input#Search_boxx1").focus(function(){
          $("#Search_boxx1").css("border","2px solid #0084b4");
          $("#Search_boxx1").css("border-right","none");
          $("#Search_boxx1").css("box-shadow","none");
          $("#glyy1").css("border","2px solid #0084b4");
          $("#glyy1").css("border-left","none");
          $("#glyy1").css("height","26");
          $("#glyy1").css("font-size","10px")
          
      });

      $("input#Search_boxx1").blur(function(){
        $("#Search_boxx1").css("border","");
        $("#Search_boxx1").css("border-right","");
        $("#glyy1").css("border","");
        $("#glyy1").css("border-left","");
        $("#glyy1").css("height","");
        $("#glyy1").css("font-size","")
        
    });

    });

    
  }

  mynews:any[]=[];
  mynewss(){
    this.obj.get("./assets/news.json").subscribe(
      data=>{
      this.mynews=data as string[];
      }
    );
  }

  follow:any[]=[];
  follows(){
    this.obj.get("./assets/who_to_follow.json").subscribe(
      data=>{
      this.follow=data as string[];
      }
    );
  }

  tweet:any[]=[];
  tweets(){
    this.obj.get("./assets/tweet_o.json").subscribe(
      data=>{
      this.tweet=data as string[];
      }
    );
  }
}

