import { Component, OnInit, ElementRef } from '@angular/core';

import { HttpClient } from '@angular/common/http';
import * as $ from 'jquery';

@Component({
  selector: 'app-tweethome',
  templateUrl: './tweethome.component.html',
  styleUrls: ['./tweethome.component.css']
})
export class TweethomeComponent implements OnInit {

  constructor(private elementRef:ElementRef,private obj:HttpClient) { }

  ngOnInit() {
    this.mynewss();
    this.follows();
    this.tweets();

    $(document).ready(function(){
      
      $("#cancel_occ").click(function(){
        $("#occupation").slideUp("fast");
        $("#profile_pic").slideDown("slow");
            
      });
      
      
      $("#cancel_pic").click(function(){
        $("#profile_pic").slideUp("fast");	
        $("#occupation").slideDown("slow");	
      });

      $("#search_b").focus(function(){
        $("#search_b").css({"height":"50x","border":"2px solid #0084b4","border-left":"none"});
        $("#jquser").css({"border":"2px solid #0084b4","border-right":"none"});
        $("#searchbar_jqheight").css("min-height","90px");
      });
      
      $("#search_b").blur(function(){
        $("#search_b").css({"height":"","border":"","border-left":""});
        $("#jquser").css({"border":"","border-right":""});
        $("#searchbar_jqheight").css("min-height","");
      });

      $("input#Search_boxx1").focus(function(){
          $("#Search_boxx1").css("border","2px solid #0084b4");
          $("#Search_boxx1").css("border-right","none");
          $("#Search_boxx1").css("box-shadow","none");
          $("#glyy1").css("border","2px solid #0084b4");
          $("#glyy1").css("border-left","none");
          $("#glyy1").css("height","26");
          $("#glyy1").css("font-size","10px")
          
      });

      $("input#Search_boxx1").blur(function(){
        $("#Search_boxx1").css("border","");
        $("#Search_boxx1").css("border-right","");
        $("#glyy1").css("border","");
        $("#glyy1").css("border-left","");
        $("#glyy1").css("height","");
        $("#glyy1").css("font-size","")
        
    });

    });

    
  }

  mynews:any[]=[];
  mynewss(){
    this.obj.get("./assets/news.json").subscribe(
      data=>{
      this.mynews=data as string[];
      }
    );
  }

  follow:any[]=[];
  follows(){
    this.obj.get("./assets/who_to_follow.json").subscribe(
      data=>{
      this.follow=data as string[];
      }
    );
  }

  tweet:any[]=[];
  tweets(){
    this.obj.get("./assets/tweet_o.json").subscribe(
      data=>{
      this.tweet=data as string[];
      }
    );
  }

  p:number=1;
}
